.profile {
  background-color: var(--grey800);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.profile img {
  width: 5.625rem;
  border-radius: 50%;
  margin-bottom: 1.875rem;
}
.profile h1 {
  letter-spacing: 0.1rem;
  margin-bottom: 1rem;
}
.profile h2 {
  font-size: 1rem;
  color: var(--green);
}
.profile p {
  font-size: 0.875rem;
}
.profile ul,
.profile li {
  width: 100%;
}
.profile li {
  margin-bottom: 1rem;
}
.profile li:last-child {
  margin-bottom: 0;
}
.profile li a {
  border-radius: 0.625rem;
  background-color: var(--grey700);
  display: inline-block;
  font-weight: 700;
  width: 100%;
  padding: 16px 0;
  text-align: center;
  color: var(--white);
  cursor: pointer;
}
.profile li a:hover {
  background-color: var(--green);
  color: var(--grey800);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  background: var(--background);
  color: var(--text-color);
  font-family: var(--font-inter);
  font-size: 16px;
  margin: 0 1rem;
  min-height: calc(100vh - 0.1px);
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.attribution {
  margin-top: 2rem;
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: var(--green);
}

:root {
  --green: hsl(75, 94%, 57%);
  --white: hsl(0, 0%, 100%);
  --grey700: hsl(0, 0%, 20%);
  --grey800: hsl(0, 0%, 12%);
  --grey900: hsl(0, 0%, 8%);
  --background: var(--grey900);
  --text-color: var(--white);
}

:root {
  --font-inter: "Inter", sans-serif;
}

.container {
  border-radius: 0.875rem;
  max-width: 384px;
  padding: 1.5625rem 1.5rem;
}
@media (min-width: 40em) {
  .container {
    width: 384px;
    padding: 2.5625rem 2.5rem;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}